import { Application, Example, Sprite } from "@danneu/solid-pixi";
import { Texture } from "pixi.js";
import type { Component } from "solid-js";
import { createResource } from "solid-js";

export const LocalExample: Component = () => {
  const [app] = createResource(async () => {
    await new Promise((resolve) => setTimeout(resolve, 1000));
    return 42;
  });

  return (
    <>
      <div>App exists: {app() ? "YES" : "NO"}</div>
      <div>App state: {app.state}</div>
      {app() ? <div>App loaded</div> : <div>No app</div>}
    </>
  );
};

export default function () {
  return (
    <div>
      test component2
      <h2>Local example</h2>
      <LocalExample />
      <h2>Lib Example</h2>
      <Example />
      <Application
        ref={(app) => {
          console.log("ref app", app);
        }}
      >
        <Sprite
          texture={Texture.WHITE}
          x={100}
          y={100}
          width={100}
          height={100}
        />
      </Application>
    </div>
  );
}
